import React from "react";
import CommonModalFooter from "../../common/CommonModalFooter";
import { Form } from "react-final-form";
import {
  InputFormGroup,
  CheckboxFormGroup,
  required,
} from "../../common/Forms";

import { useDispatch } from "react-redux";
import { actionCRUDUserProfile } from "../../../actions/user/actionUserProfile";
import bootstrap from "bootstrap/dist/js/bootstrap.bundle";

const FormUser = ({ user }) => {
  const dispatch = useDispatch();

  const onDelete = () => {
    let newLine = "\r\n";
    let confirm_alert = "Tem certeza que gostaria de deletar este Usurio?";
    confirm_alert += newLine;
    confirm_alert += "Usurio: " + user.username;
    if (window.confirm(confirm_alert)) {
      dispatch(actionCRUDUserProfile.delete(user.id));
      bootstrap.Modal.getInstance(document.getElementById("ModalUser")).hide();
    }
  };

  const onSubmit = (values) => {
    let closeModal = false;
    if (values.id !== undefined) {
      if (values.password === undefined || values.password === "") {
        delete values["password"];
      }
      if (values.id === 0) {
        dispatch(actionCRUDUserProfile.create(values));
        closeModal = true;
      } else {
        dispatch(actionCRUDUserProfile.update(values));
        closeModal = true;
      }
    }
    if (closeModal) {
      bootstrap.Modal.getInstance(document.getElementById("ModalUser")).hide();
    }
  };
  return (
    <Form
      initialValues={user}
      onSubmit={onSubmit}
      render={({ handleSubmit, form }) => (
        <form onSubmit={handleSubmit} className="needs-validation" noValidate>
          <div className="modal-body container">
            <div className="container">
              <div className="form-inline">
                <InputFormGroup
                  name="username"
                  label="Usurio:"
                  className="m-1"
                  validate={required}
                  format={(value) => (value ? value.toLowerCase() : "")}
                />
              </div>
              <div className="form-inline">
                <InputFormGroup
                  name="first_name"
                  label="Nome:"
                  className="m-1"
                  validate={required}
                />
              </div>
              <div className="form-inline">
                <InputFormGroup
                  name="last_name"
                  label="Sobrenome:"
                  className="m-1"
                />
              </div>
              <div className="form-inline">
                <InputFormGroup name="email" label="Email:" className="m-1" />
              </div>
              <div className="form-inline">
                <InputFormGroup
                  name="profile.matricula"
                  label="Matrcula:"
                  className="m-1"
                />
              </div>
              <CheckboxFormGroup name="is_staff" label="Administrador" />
              <CheckboxFormGroup name="is_active" label="Ativo" />
              <div className="form-inline">
                <InputFormGroup
                  name="password"
                  label="Senha:"
                  className="m-1"
                  validate={(value) =>
                    value
                      ? value.length >= 4
                        ? undefined
                        : "A senha deve ter ao menos 6 caracteres"
                      : undefined
                  }
                />
              </div>
              <span>
                Obs.: Senha em branco no ir alterar a senha do usurio.
              </span>
            </div>
          </div>
          <CommonModalFooter
            canDelete={
              user !== undefined ? (user.id !== 0 ? true : false) : false
            }
            canCopy={false}
            onDelete={onDelete}
            form={form}
          />
        </form>
      )}
    />
  );
};

export default FormUser;
